<template>
  <div id="app">    
    <l-map ref="myMap" id="map" :zoom.sync="zoom" :center="center">      
      <l-tile-layer 
        :url="tileProvider.url"
        :attribution="tileProvider.attribution" />
      <l-marker :lat-lng.sync="marker.position">
      </l-marker>
    </l-map>
  </div>
</template>

<script>
import {LMap, LMarker, LTileLayer } from 'vue2-leaflet';

export default {
    name: 'app',
    components: {
        LMap,
        LTileLayer,
        LMarker
    },
    data() {
      return {
        marker: {
          position: { lng: -1.219482, lat: 47.41322 }
        },
        tileProvider: {
          name: 'OpenStreetMap',
          visible: true,
          attribution:
            '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
          url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
        },
        zoom: 3,
        center: [47.41322, -1.219482]
      }
    },
    mounted(){
      this.$getLocation({
          
          timeout: 1000, //defaults to Infinity
          
      })
        .then(coordinates => {
          this.marker.position.lng = coordinates.lng
          this.marker.position.lat = coordinates.lat
          this.center = [coordinates.lat, coordinates.lng]
        });      
    }
}
</script>

<style>
html, body, #app /*, and all other map parent selectors*/ {
  height: 100%;
  overflow: hidden;  
}
#map {
  width: auto;
  height: 98%;
}
</style>
